<template>
  <div>
    <div class="content">
      <div class="ocps-CommissionRate">
        <ul>
          <li v-for="(key,value,index) in data" >
            <p class="ocps-CommissionRate_lf lf">{{value}}</p>
            <p class="ocps-CommissionRate_rt rt">{{key}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'DetailInquiry',
    data(){
    return {
      data:this.$route.query.res
    }
  }
  }

</script>



